import { useEffect, useState } from 'react'
import _ from 'lodash'
import Axios from './axios'
import Title from "./Components/Title"
import Search from "./Components/Search"
import Form from "./Components/Form"
import Result from "./Components/Result"
import { useDispatch, useSelector } from "react-redux";
import { fetchUser } from './store/getUsers/getUser'


const App = () => {
  const [newName, setNewName] = useState('')
  const [newNum, setNewNum]= useState('')
  const [query, setQuery]= useState('')
  const [result, setResult]= useState([{}])
  const dispatch = useDispatch()
  const user = useSelector(state=>state.user)
  async function fetchData() {
    dispatch(fetchUser())
  }
  useEffect(()=>{
      fetchData()
  },[])

  

  const handleSubmit = (e)=>{
    e.preventDefault()
    fetchData()
    const addPerson = {
       name: newName,
       number: newNum
    };

   
    const check = user.Persons.filter((person)=>person.name.toLowerCase()===newName.toLowerCase())
    const convCheck = Object.assign({},check.name)
    
        
   
    console.log(convCheck)
    console.log(newName)
    console.log(_.isEqual(convCheck.name,addPerson.name))
    if(_.isEqual(convCheck.name, addPerson.name)){
      
      alert(`${newName} already exists`)
    }
    
    else{
       Axios
        //  .create(addPerson).then(response => setPersons(persons.concat(response.data)))
            .create(addPerson)
    }
    
    
  }

  const handleChange= (e)=>{
    setNewName(e.target.value)
  }

  const handleNumChange= (e)=>{
    setNewNum(e.target.value)
  }

  const handleSearch= (e) =>{
    setQuery(e.target.value);
    const searchHandler = user.Persons.filter((person) => person.name.toLowerCase().includes(query.toLowerCase()))
    setResult(searchHandler);
  }



  const HandleDelete=(id,e)=>{
    e.preventDefault()  
    Axios
        .Deleteid(id).then(console.log("Deleted"))
    fetchData()
  }

  


  



  return (
    
    <div>
      <div className='title-header container'>
        <Title titleHeader={"Phonebook"}/>
      </div>
     
      <div className='container bg-1'>
      <input className='search-box' type="text" value={query} onChange={handleSearch} placeholder="Enter Contact Name"/>
      {result.map((res,index)=>
        <Search name={res.name} number = {res.number} index={index} />
      )}
      </div>

      <div className='title-header form-container bg-1'>
        <Title titleHeader={"Add New Contact"}/>
        <Form handleSubmit={handleSubmit} newName={newName} handleChange={handleChange} newNum={newNum} handleNumChange={handleNumChange} />
      </div> 
      
      <div className='title-header container bg-1'>
        <Title titleHeader={"Numbers"}/>

      {user.Persons.map((person,index)=>
        
          <Result index={index} person={person} HandleDelete={HandleDelete}/>
          
      )}
      </div>
      
      
    </div>
  )
}

export default App