import React from "react"

const Form =({handleSubmit,newName,newNum,handleChange,handleNumChange})=>{
    return(
    <form onSubmit={handleSubmit}>
        <div>
          <label>Name: </label><input value={newName}  onChange={handleChange}/>
        </div>
        <div>
          <label>Number:</label><input value={newNum} onChange={handleNumChange} />
        </div>
         <div className="Button-spacer">
          <button className="Add-bt" type="submit">Add</button>
        </div>
    </form>
    )
}

      
  

  export default Form
  